        office_address: ['', '', '', ''],
        household_income: '',
        personal_income: '',
        houseloan: '',
        housing: '',
        monthly_invest: '',
        finance_tool: [],
        vehicle: '',
        group: [], 
        wg_name: ''
      }
    }
  },
  watch: {
    'form.identityID': function () {
      this.form.identityID = this.form.identityID.replace(/[^a-zA-Z0-9]+/g,"")
    },
    'form.lineid' : function () {
      this.form.lineid = this.form.lineid.replace(/[^0-9a-zA-Z_\-]+/g,"")
    },
  },
  computed: {
    ...mapGetters(['Cities'])
  },
  methods: {
    //  監聽input 只能輸入數字
    numberMobile(){
      this.form.mobile = this.form.mobile.replace(/[^\d]/g,'');
    },
    numberPhone0(){
      this.form.phone[0] = this.form.phone[0].replace(/[^\d]/g,'');
    },
    numberPhone1(){
      this.form.phone[1] = this.form.phone[1].replace(/[^\d]/g,'');
    },
    numberOffice_phone0(){
      this.form.office_phone[0] = this.form.office_phone[0].replace(/[^\d]/g,'');
    },
    numberOffice_phone1(){
      this.form.office_phone[1] = this.form.office_phone[1].replace(/[^\d]/g,'');
    },
    numberOffice_phone2(){
      this.form.office_phone[2] = this.form.office_phone[2].replace(/[^\d]/g,'');
    },
    numberFax_area(){
      this.form.fax_area = this.form.fax_area.replace(/[^\d]/g,'');
    },
    numberFax(){
      this.form.fax = this.form.fax.replace(/[^\d]/g,'');
    },
    
    ...mapActions([
      'setPageLock' , 'updateCustID' , 'updateSerNo'
    ]),
    removeGroupItem (i) {
      this.form.group.splice(i, 1)
    },
    submitGroup (group) {
      this.form.group.push(group)
      this.panelClose('groupModalOpen')
    },
    submitClient (member) {
      this.form.introducer = member.name
      this.form.introducerId = member.id
      this.panelClose('clientSearchOpen')
    },
    initData() {
      let form = this.form
      for(var key in form) {
        switch(key) {
          case 'birthday':
            form[key] = ['', '','']
            break;
          case 'phone':
            form[key] = ['', '']
            break;
          case 'office_phone':
            form[key] = ['', '','']
            break;
          case 'address':
            form[key] = ['', '','']
            break;
          case 'office_address':
            form[key] = ['', '','']
            break;
          case 'finance_tool':
            form[key] = ['0','0','0','0','0','0','0','0','0']
            break;
          case 'group':
            form[key] = []
            break;
          default:
            form[key] = ''
        }
      }
    },
    initGroupsAndFinanceStatus() {
      this.form.household_income = ''
      this.form.houseloan = ''
      this.form.personal_income = ''
      this.form.housing = ''
      this.form.monthly_invest = ''
      this.form.vehicle = ''
      this.form.finance_tool = []
      this.form.group = []
    },
    updateDate () {
      if (!this.form.birthday) return

      let dt = window.moment().year(this.form.birthday[0]).month(this.form.birthday[1] - 1).endOf('month')
      this.birthDateOpt = []
      for(var i = 0; i < dt.date(); i++) {
        this.birthDateOpt.push(i + 1)
      }
    },
    changeCity (key) {
      if(key === 'address' && this.form.address[0].length > 0) {
        queryDistByCity(this.$store.state.userInfo.id, this.form.address[0]).then(result => {
          this.areaOpt = result.body.cityAreaList.map(item => {return {area: item.AREA, city:item.CITY, zipcode:item.ZIPCODE}})
          this.form.address[1] = ''
          this.form.address[2] = ''
        })
        
        // this.areaOpt = [{city: '台北市', area:'中正區', zipcode: '022'}, {city: '台北市', area:'大同區', zipcode: '011'}]
      } else if(key === 'office_address' && this.form.office_address[0].length > 0) {
        queryDistByCity(this.$store.state.userInfo.id, this.form.office_address[0]).then(result => {
          this.officeAreaOpt = result.body.cityAreaList.map(item => {return {area: item.AREA, city:item.CITY, zipcode:item.ZIPCODE}})
          this.form.office_address[1] = ''
          this.form.office_address[2] = ''
        })
        // this.officeAreaOpt = [{city: '台北市', area:'中正區', zipcode: '022'}, {city: '台北市', area:'大同區', zipcode: '011'}]
      }
      // let index = 0
      // this.Cities.forEach((c, i) => {
      //   if (c.city === this.form[key]) index = i
      // })
      // if (key === 'address') {
      //   this.areaOpt = this.areaList[index]
      // }
      // if (key === 'office_address') {
      //   this.officeAreaOpt = this.areaList[index]
      // }
    },
    checkDataFormat() {
      return this.checkName() && this.checkContactInfo() && this.checkPersonalInfo()
    },
    checkName() {
      // 不能為空,特殊符號,數字
      let pattern = /^[a-zA-Z\u4e00-\u9fa5\s]+$/g
      return pattern.test(this.form.name)
      // return true
    },
    checkContactInfo() {
      let linePattern = new RegExp('^[A-Za-z0-9_]+$')
      let mobilePattern = new RegExp('^[0-9]{10}$')
      let phoneAreaPattern = new RegExp('^0[1-9]{1,3}$')
      let phonePattern = new RegExp('^[0-9]{7,8}$')

      let allResult = [
        this.checkLine(),
        this.checkMobile(),
        (this.checkPhone('home')),  
        (this.checkPhone('office'))
      ]
      return allResult.indexOf(true) > -1
      // return true
    },
    checkLine() {
      if(this.form.lineid == '') { return true } 
      let linePattern = new RegExp('^[A-Za-z0-9_]+$')
      return linePattern.test(this.form.lineid)
    },
    checkMobile() {
      
      if(this.form.mobile == '') { return true } 
      let mobilePattern = new RegExp('^[0-9]{10}$')
      return mobilePattern.test(this.form.mobile)
    },
    checkPhone(target) {
      if(this.form.phone[0] == '' && this.form.phone[1] == '') { return true } 
      let phoneAreaPattern = new RegExp('^0[0-9]{1,3}$')
      let phonePattern = new RegExp('^[0-9]{7,10}$')
      let extPattern = /^[0-9]{0,8}$/
      switch (target) {
        case 'home':
          return phoneAreaPattern.test(this.form.phone[0]) && phonePattern.test(this.form.phone[1])
        case 'office':
          return phoneAreaPattern.test(this.form.office_phone[0]) && phonePattern.test(this.form.office_phone[1]) && extPattern.test(this.form.office_phone[2])
        default:
          return false
      }
    },
    checkPersonalInfo() {
      return this.checkEmail() && this.checkFaxPhone()
    },
    checkEmail() {
      if(this.form.email == '') {
        return true
      } else {
        let emailPattern = /^[\w\-\.]+\@\w+(\.\w+)+$/g
        return emailPattern.test(this.form.email)
      }
    },
    checkFaxPhone() {
      if(this.form.fax_area == '' && this.form.fax == '') {
        return true
      } else {
        let faxAreaPattern = new RegExp('^0[1-9]{1,3}$')
        let faxPattern = new RegExp('^[0-9]{7,8}$')

        let allResult = [
          faxAreaPattern.test(this.form.fax_area),
          faxPattern.test(this.form.fax)
        ]
        return allResult.indexOf(false) === -1
      }
    },
    checkWordNum(){
      // 判斷輸入欄位字數是否超過限制
      if(this.form.name.length > 50 || this.form.wg_name.length > 150 ||
      this.form.identityID.length > 10 || this.form.lineid.length > 20 || this.form.mobile.length >20 ||
      this.form.phone[0].length > 5 || this.form.phone[1].length > 20 || this.form.office_phone[0].length > 5 ||
      this.form.office_phone[1].length > 15 || this.form.office_phone[2].length > 10 || this.form.email.length > 50 ||
      this.form.agency.length > 30 || this.form.address[2].length > 50 || this.form.office_address[2].length > 50 ||
      this.form.fax_area.length > 5 || this.form.fax.length > 20 || this.form.houseloan.length > 10){
        return true
      }else{
        return false
      }
    },
    doSubmit () {
      
      if(this.checkWordNum()){
        return this.$toast.info({title: '欄位總字數超過限制'})
      }
      
      if(this.form.identityID.trim().length == 0) {
        this.initGroupsAndFinanceStatus()
      }
      
      if(this.checkDataFormat()) {
        let insertData = Object.assign({}, this.form)

        if(!this.checkLine()) {
          insertData.lineid = ''
          // delete insertData.lineid
        }
        if(!this.checkMobile()) {
          insertData.mobile = ''
          // delete insertData.mobile
        }
        if(!this.checkPhone('home')) {
          insertData.phone[0] = ''
          insertData.phone[1] = ''
          // delete insertData.phone
        }
        if(!this.checkPhone('office')) {
          insertData.office_phone[0] = ''
          insertData.office_phone[1] = ''
          insertData.office_phone[2] = ''
          // delete insertData.office_phone
        }
        if(!this.checkEmail()) {
          insertData.email = ''
          // delete insertData.email
        }
        if(!this.checkFaxPhone()) {
          insertData.fax_area = ''
          insertData.fax = ''
          // delete insertData.fax_area
          // delete insertData.fax
        }

        let defaultFinanceTool = ['0','0','0','0','0','0','0','0','0']
        this.form.finance_tool.map(item => defaultFinanceTool[Number(item) -1] = item)
        insertData.finance_tool = defaultFinanceTool.join('')  //理財工具

        let areaOptObj = this.areaOpt.filter(item => item.area == this.form.address[1])[0]
        insertData.address[3] = areaOptObj? areaOptObj.zipcode : ''  //地址

        let officeAreaOptObj = this.officeAreaOpt.filter(item => item.area === this.form.office_address[1])[0]
        insertData.office_address[3] = officeAreaOptObj? officeAreaOptObj.zipcode : ''  //公司地址
        let month = insertData.birthday[1].toString().length == 1? '0' + insertData.birthday[1].toString(): insertData.birthday[1].toString()
        let date = insertData.birthday[2].toString().length == 1? '0' + insertData.birthday[2].toString(): insertData.birthday[2].toString()
        insertData.birthday = [insertData.birthday[0].toString(), month, date]

        addNewCust(this.$store.state.userInfo.id, insertData).then(result => {
          if(result.header.status && result.header.status === '200'){
            this.$toast.info({title: '「新增客戶」已完成'})
            if(result.body.ser_no){  //如果客戶是無 ID
              this.updateSerNo(result.body.ser_no)
              this.updateCustID('')
              localStorage.SerNo = result.body.ser_no
              localStorage.CustID = ''
              this.$router.push({path: `/detail/SerNo/tab/1`})
            }else{                   //如果客戶有 ID
              this.updateCustID(this.form.identityID)
              this.updateSerNo('')
              localStorage.SerNo = ''
              localStorage.CustID = this.form.identityID
              this.$router.push({path: `/detail/CustID/tab/1`})
            }
          } else {
            if(result.header.message){
              // 這邊要處理 message 待處理
              this.$toast.info({title: `「新增客戶」失敗:${result.header.message}`})
            }else{
              this.$toast.info({title: `「新增客戶」失敗 , 狀態碼:${result.header.status}`})
            }
          }
        })
      } else {
        let checkNameMsg = this.checkName()? '': '姓名不能為空,特殊符號,數字'
        let checkContactMsg = this.checkContactInfo()? '': '聯絡資訊格式有誤'
        let emailMsg = this.checkEmail()? '': 'email格式有誤'
        let faxMsg = this.checkFaxPhone()? '': '傳真電話格式有誤'
        let toastMsg = [checkNameMsg, checkContactMsg, emailMsg, faxMsg].filter(msg => {return msg != ''}).join(',')
        this.$toast.info({title: toastMsg})
      }
      // if (
      //   this.form.name==='' ||
      //   (this.form.lineid==='' && this.form.mobile==='' && this.form.phone && this.form.office_phone)
      // ) {
      //   this.panelOpen('validModalOpen')
      // } else {
      //   this.$router.push('/detail/1/empty')
      //   this.$toast.info({title: '「新增客戶」已完成'})
      // }
    }
  },
  mounted () {
    let start = this.today.year() - 100
    // let end = this.today.year() - 18

    // for(var i = start; i < end; i++) {
    //   this.birthYearOpt.push(i)
    // }
    this.birthYearOpt = Array.apply(null, {length: 101}).map(function(value,index){ return index + start })
    
    // this.Cities.forEach(ci => {
    //   this.cityOpt.push(ci.city)
    //   this.officeCityOpt.push(ci.city)
    //   this.areaList.push(ci.area)
    // })
    this.cityOpt = this.Cities.map(item => item.city)
    this.officeCityOpt = this.Cities.map(item => item.city)
    this.areaList = this.Cities.map(item => item.city)

    this.updateDate()
    this.changeCity('address')
    this.changeCity('office_address')
    this.$validator.localize('zh_TW');
    // this.$validator.localize('zh_TW', {
    //   messages: {
    //     required: (field) => '* ' + field + ' is required'
    //   },
    //   attributes: {
    //     // email: '信箱'
    //   } 
    // })
  }
}
</script>
