<template>
  <div class="modal fade" :class="{'is-open': open}">
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-body">
          <section class="search">
            <div class="row">
              <div class="col-10 offset-1">
                <p class="text-center">請以關鍵字搜尋客戶後選擇其中一位：</p>
                <form class="form">
                  <div class="input-group input-lg">
                    <div class="input">
                      <input
                      type="text"
                      name="keyword"
                      id="keyword"
                      class="form-control"
                      
                      v-model="keyword"
                      placeholder="輸入客戶姓名進行搜尋">
                    </div>
                    <button
                    class="btn btn-primary btn-icon"
                    type="submit"
                    @click.stop.prevent="findClient()">
                      <i class="icon-search i-24"></i>
                    </button>
                  </div>
                </form>
              </div>
            </div>
          </section>
          <section class="maintain mt-5">
            <div class="row">
              <div class="col-10 offset-1">
                <div class="row">
                  <div class="col-6"
                  v-for="(c, cid) in clientList"
                  :key="`search-client-${cid}`">
                    <div class="radio">
                      <input
                      type="radio"
                      name="selection"
                      :id="`selection-${cid}`"
                      v-model="selection"
                      :value="c">
                      <label :for="`selection-${cid}`" class="scrollData">
                        {{ c.name.length > 17 ? c.name.substr(0,16) + '. . . ' :  c.name }}({{ c.birthday | ageCounter }}歲)
                      </label>
                    </div>
                  </div>
                  <div v-if="clientLoading" :class="{'section-loading': true}" class="col-12 text-center">
                    <h1 class="m-5">Loading</h1>
                  </div>
                </div>
              </div>
            </div>
          </section>
          <section class="action text-right">
            <a
            class="btn btn-default"
            @click="$emit('close')">
              取消
            </a>
            <a
            class="btn btn-primary"
            @click="doSubmit">
              確認
            </a>
          </section>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getCustomers } from '../../service'

const clientList = [
  // {
  //   name: '林怡君',
  //   birthday: '19950208'
  // },
  // {
  //   name: '林明禎',
  //   birthday: '19800208'
  // },
  // {
  //   name: '林曉君',
  //   birthday: '19820208'
  // },
  // {
  //   name: '林飛飛',
  //   birthday: '19760208'
  // },
  // {
  //   name: '林進',
  //   birthday: '19840208'
  // }
]

export default {
  name: 'SearchClientModal',
  props: {
    open: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      clientLoading: false,
      error: false,
      keyword: '',
      list: [],
      selection: null,
      clientList: []
    }
  },
  watch: {
    'keyword': 'initList'
  },
  methods: {
    initList () {
      this.list = []
    },
    findClient () {
      this.queryCustList()
      if (this.keyword === '') {
        this.initList()
      } 
      // else {
      //   this.list = clientList.filter(item => {
      //     return item.name.indexOf(this.keyword) > -1
      //   })
      // }
    },
    doSubmit () {
      console.log(this.selection);
      
      this.$emit('submit', this.selection)
    },
    queryCustList(){
      this.clientLoading = true
      this.clientList = []
      getCustomers(this.$store.state.userInfo.id, this.keyword).then(result =>{
        if(result.body) {
          this.clientList = result.body.customer_list.map(eachCust => {
            return {
              name : eachCust.name || '',
              birthday : eachCust.birthday || '',
              id : eachCust.id
            }
          })
        } else {
          this.clientList = []
        }
        this.clientLoading = false
      })
    }
  }
}
</script>


